import Vue from 'vue'
import App from './App'
import router from './router'
import Navigation from './Homepage.vue'

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  render: h => h(App),
  router,
  components: { App, Navigation },
  template: '<App/>'
})



